img {
   height: 220px;
   background-color: rgba(167, 167, 167, 0.651);
   border-radius: 50%;
   transform: scaleX(-1);
   margin-left: 20px;
}

.circle-container {
   display: flex;
   margin-bottom: 0px;
   align-items: center;
   justify-content: space-evenly;
}

.circle {
   background-color: rgba(167, 167, 167, 0.651);
   width: 100px;
   height: 100px;
   border-radius: 100px;
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
   box-shadow: -10px 10px rgba(0, 0, 0, 0.808);
   transition: 0.5s;
}

.circle:hover {
   box-shadow: 0px 0px rgba(0, 0, 0, 0.658);
}

.second-part {
   display: flex;
   flex-direction: column;
   /* justify-content: space-around; */
   align-content: space-between;
   /* background-color: tomato; */
   height: max-content;
}
.responsive-container {
   height: 400px;
}

@media only screen and (max-width: 620px) {
   /* For mobile phones: */
   img {
      width: 200px;
      height: 200px;
      border-radius: 100px;
      margin: 0px;
      margin-top: 30%;
      display: block;
   }
   .responsive-container {
      max-height: 350px;
      overflow: auto;
   }
   .circle-container {
      margin: 0px;
      margin-bottom: 10px;
   }
}
