.header {
   width: 90vw;
   display: flex;
   align-content: space-between;
   align-items: center;
   justify-content: space-between;
   justify-items: center;
}

.header-text {
   width: 40%;
   display: flex;
   margin-left: 2%;
   margin-top: -0.6%;
}

.header-p {
   margin-top: 8%;
   margin-left: 3%;
}

.link-box {
   /* background-color: blanchedalmond; */
   width: 30%;
   display: flex;
   align-content: flex-end;
   justify-content: space-around;
   margin-top: -2%;
}

@media only screen and (max-width: 620px) {
   .subtitle {
      display: none;
   }
   .header {
      flex-direction: column;
      width: 100%;
      height: 100%;
      margin-top: 1%;
   }
   .header-p {
      margin: 0%;
   }
   .header-text {
      width: 100%;
      justify-content: center;
      margin: 0%;
   }
   .link-box {
      width: 100%;
      margin: 0%;
      margin-bottom: 10px;
   }
}
