.footer {
   background-color: rgba(0, 0, 0, 0.3);
   box-shadow: -10px 10px rgba(0, 0, 0, 0.658);
   border-radius: 25px;
   width: 90%;
   height: 16%;
}

.footer-content {
   display: flex;
   justify-content: space-evenly;
   align-items: center;
   /* margin-top: -1%; */
}

@media only screen and (max-width: 620px) {
   /* For mobile phones: */
   .footer {
      width: 100%;
      height: max-content;
      align-content: center;
   }
   .footer-content {
      flex-direction: column;
      margin: 0%;
      align-content: space-around;
      justify-content: unset;
   }
   .row {
      display: flex;
      flex-direction: row;
   }
}
