.App {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-evenly;
   justify-self: center;
   height: 100vh;
}

.App-logo {
   height: 40vmin;
   pointer-events: none;
}

.App-header {
   background-color: #282c34;
   min-height: 100vh;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   font-size: calc(10px + 2vmin);
   color: white;
}

.App-link {
   color: #61dafb;
}

/* h1 {
   font-size: 3vw;
} */

.link {
   color: white;
   text-decoration: none;
}

.main-container {
   background-color: rgba(0, 0, 0, 0.3);
   box-shadow: -10px 10px rgba(0, 0, 0, 0.658);
   border-radius: 25px;
   width: 90%;
   height: max-content;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
}

@media only screen and (max-width: 620px) {
   /* For mobile phones: */
   .main-container {
      width: 100%;
      flex-direction: column;
      min-height: fit-content;
   }
}
